
#partOne {
    position: fixed;
    top: 0vh;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    background-image: url("/public/assets/imgs/Bg2.jpg");
    overflow-y: scroll;
    overflow-x: hidden;
    direction: rtl;
}

.circle-div{
    border: 1px solid white;
    border-top-left-radius: 53.5vh;
    border-top-right-radius: 53.5vh;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background-color: white;
    height: 83vh;
    width: 107vh;
    right: 50%;
    transform: translateX(50%);
    position: absolute;
    margin-top: 63vh;
}



