
.video-step2 {
    position: fixed;
    top: 10vh;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    align-items: center;
    z-index: 9990;
    direction: rtl;
}



.video-info2{
    font-family: "Heebo";
    font-size: 1.2em;
    font-weight: 400;
    position: relative;
    right: 50%;
    transform: translateX(50%);
    width: 90vw;
    color: #535353;
    text-align: center;
    margin-top: 30vh;
}
.next-step-button-2{
    margin-top: 30px;
    padding: 10px 25px;
    font-size: 1.5em;
    border-radius: 30px;
    background-color:#1bbfe5;
    color: white;
    border: none;
    font-family: "Heebo";
    cursor: pointer;
    width:60vw;
}